<template>
	<view>
		<view class="btn">
			<view class="qd" @click="bfa">播放</view>
		</view>
	</view>
</template>
<script>
	const webstomp = require('webstomp-client/dist/webstomp.js')
	const sockjs = require('@/utils/sockjs.js')
	const Client = require('stompjs')
	
	export default {
		name: 'messageNotice',
		data() {
			return {
				address: '',
				client: null,
				
			}
		},
		onLoad() {
		},
		mounted(){
		    this.connects();
		  },
		methods:{
			connects() {
				var PROTOCOL = 'ws'     //连接协议
				var IP = '172.17.36.23'  //ip
				var PORT = '15674'      //端口
				var ADDRESS = 'ws'  
				var ACCOUNT = 'guest'   //账号
				var PASSWORD = 'guest'  //密码
				var address = PROTOCOL + '://' + IP + ':' + PORT + '/' + ADDRESS;
			    this.client = webstomp.client(address)
			      this.client.connect(ACCOUNT, PASSWORD, () => {
			        console.log('连接成功!')
			        // 连接成功跳转方法
			        this.subscribe()
			      }, (e) => {
			        console.log('连接失败!')
			        // 失败跳转方法
			        this.error()
			      })
			    },
			    subscribe(){
			      this.client.subscribe("/queue/queue.message", (data)=> {
			      // data接收的是json类型的数据，所以要进行转换
			        var message = JSON.parse(data.body)
					console.log("333333333333",message.messageType);
					if(message.messageType == '3'){
						console.log("可以")
						this.bfa()
					}else{
						console.log("不行cccc");
					}
			      })
			    },
			    error(err){
			      console.log('555',err)
			    },
	
			bfa(){
				let innerAudioContext = uni.createInnerAudioContext()
				innerAudioContext.volume = 1
				innerAudioContext.src = '/static/audio/new.mp3'
				innerAudioContext.play()
				innerAudioContext.onError((res) => {
				  console.log(res.errMsg);
				  console.log(res.errCode);
				});
			},
			bfb(){
				let innerAudioContext = uni.createInnerAudioContext()
				innerAudioContext.volume = 1
				innerAudioContext.src = '/static/audio/urge.mp3'
				innerAudioContext.play()
				innerAudioContext.onError((res) => {
				  console.log(res.errMsg);
				  console.log(res.errCode);
				});
			},
			
		}
	}
</script>

<style>
	.btn{
		background-color: #05c0ab;
		width: 80%;
		margin-left: 10%;
		text-align: center;
		border-radius: 30rpx;
		padding: 30rpx 0;
	}
	.qd{
		color: #ffffff;
		font-size: 30rpx;
	}
</style>